import React, { Component } from 'react';
import axios from 'axios';
import {Table, Grid, Navbar, PageHeader} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/css/bootstrap-theme.css';
import TableHeader from './TableHeader.js'
import TableData from './TableData.js'

const baseUrl = 'https://fcctop100.herokuapp.com/api/fccusers/top/';

class App extends Component {
    constructor(props){
        super(props);
        this.state = {
            rawData: [],
            tableData: []
        };
        this.makeGetRequest = this.makeGetRequest.bind(this);
    }

    makeGetRequest(filter){
        let url = baseUrl + filter
        axios.get(url).then((result) =>{
            let userData = result.data.map((userObj, index) =>{
                return (
                    <TableData
                    index={index + 1}
                    avatar={userObj["img"]}
                    username={userObj["username"]}
                    alltime={userObj["alltime"]}
                    recent={userObj["recent"]}
                    key={index}
                    />
                );
            });
            this.setState({tableData: userData})
        });
    }

    componentDidMount(){
        this.makeGetRequest('recent');
    }

    render() {
        return (
            <div className="App">
            <Grid>
            <PageHeader>
            <Navbar inverse>
            <Navbar.Header>
            <Navbar.Brand className="text-center">
            <a href="#">Camper Leaderboad (An FCC Project)</a>
            </Navbar.Brand>
            <Navbar.Toggle />
            </Navbar.Header>
            </Navbar>
            </PageHeader>
            <Table striped bordered hover>
            <TableHeader getUserData={this.makeGetRequest}/>
            <tbody>
            {this.state.tableData}
            </tbody>
            </Table>
            </Grid>
            </div>
        );
    }
}

export default App;



